<template>
  <div class="container">
    <h2 class="text-center">設定倒數時間</h2>
    <!-- 訊息提示框 -->
    <Alert
      :MODE="MSG.MODE"
      :MSGTYPE="MSG.TYPE"
      :TITLE="MSG.TITLE"
      :TEXT="MSG.TEXT"
      @alert-check="(isClick, OKorCancel) => (alertCheck = { isClick, OKorCancel })"
    />
    <button class="btn btn-outline-secondary btn-sm mb-3" type="button" @click="$router.go(-1)">返回上一頁</button>
    <div class="row">
      <div class="col-12">
        <div class="input-group mb-3">
          <label class="input-group-text" for="yearSelect">年</label>
          <select class="form-select form-select-lg" id="yearSelect" v-model="selectedData.year">
            <option v-for="item in optionsData.year" :value="item">{{ item }}</option>
          </select>
        </div>
      </div>
      <div class="col-12">
        <div class="input-group mb-3">
          <label class="input-group-text" for="monthSelect">月</label>
          <select class="form-select form-select-lg" id="monthSelect" v-model="selectedData.month">
            <option v-for="item in optionsData.month" :value="item">{{ item }}</option>
          </select>
        </div>
      </div>
      <div class="col-12">
        <div class="input-group mb-3">
          <label class="input-group-text" for="daySelect">日</label>
          <select class="form-select form-select-lg" id="daySelect" v-model="selectedData.day">
            <option v-for="item in optionsData.day" :value="item">{{ item }}</option>
          </select>
        </div>
      </div>
      <div class="col-12">
        <div class="input-group mb-3">
          <label class="input-group-text" for="hourSelect">時</label>
          <select class="form-select form-select-lg" id="hourSelect" v-model="selectedData.hour">
            <option v-for="item in optionsData.hour" :value="item">{{ item }}</option>
          </select>
        </div>
      </div>
      <div class="col-12">
        <div class="input-group mb-3">
          <label class="input-group-text" for="minuteSelect">分</label>
          <select class="form-select form-select-lg" id="minuteSelect" v-model="selectedData.minute">
            <option v-for="item in optionsData.minute" :value="item">{{ item }}</option>
          </select>
        </div>
      </div>
      <div class="col-12 d-grid gap-2 my-2">
        <button class="btn btn-primary btn-lg" type="button" @click="setup">確定</button>
      </div>
    </div>
  </div>
</template>

<script>
import Alert from "@/components/Alert.vue";

export default {
  name: "CountDownEdit",
  components: {
    Alert,
  },
  data() {
    return {
      // 訊息提示框資料容器
      MSG: {
        MODE: "",
        TYPE: "",
        TITLE: "",
        TEXT: [],
      },
      // 訊息確認[開關]
      alertCheck: false,
      // 訊息功能碼
      funcNum: -1,
      optionsData: {
        year: [],
        month: [],
        day: [],
        hour: [],
        minute: [],
      },
      selectedData: {
        year: "",
        month: "",
        day: "",
        hour: "",
        minute: "",
      },
    };
  },
  methods: {
    setup() {
      // 製作時間字串
      const timeStr = `${this.selectedData.year + 1911}-${this.selectedData.month}-${this.selectedData.day} ${this.selectedData.hour}:${
        this.selectedData.minute
      }:00`;
      const countDown = new Date(timeStr);
      // 不可以設置過去的時間
      let des = countDown - new Date();
      if (des < 0) {
        // 訊息提示框
        this.alertVisible = true;
        this.MSG.MODE = "OKOnly"; // 形式為：OKOnly、OKCancel
        this.funcNum = 1;
        this.MSG.TYPE = "danger"; // primary成功, warning警告, danger錯誤
        this.MSG.TITLE = "系統錯誤"; // 系統提示，系統警告，系統錯誤
        this.MSG.TEXT = ["不可以設置過去的時間。"]; // 內容
      } else {
        this.$http
          .post("api/systemManage/updateCountDown", { countDown: countDown })
          .then((res) => {
            if (res.data.code === 200) {
              // 訊息提示框
              this.alertVisible = true;
              this.MSG.MODE = "OKOnly"; // 形式為：OKOnly、OKCancel
              this.funcNum = 2;
              this.MSG.TYPE = "primary"; // primary成功, warning警告, danger錯誤
              this.MSG.TITLE = "系統提示"; // 系統提示，系統警告，系統錯誤
              this.MSG.TEXT = ["已設置倒數時間。", "設置時間：" + res.data.time]; // 內容
            } else {
              console.log(res.data);
            }
          })
          .catch((err) => {
            console.log(err);
          });
      }
    },
    hideAlert() {
      this.$store.commit("SET_ALERT_VISIBLE", false);
      this.alertVisible = false;
    },
  },
  mounted() {
    let now = new Date();
    // 自動生成年份選項，今年往後推算10年
    let year = now.getFullYear() - 1911;
    for (let i = 0; i < 10; i++) {
      this.optionsData.year.push(year + i);
    }
    // 自動生成月份選項，小於10的數字前面補0
    for (let i = 1; i < 13; i++) {
      if (i < 10) this.optionsData.month.push("0" + i);
      else this.optionsData.month.push(i);
    }
    // 自動生成日期選項，小於10的數字前面補0
    for (let i = 1; i < 32; i++) {
      if (i < 10) this.optionsData.day.push("0" + i);
      else this.optionsData.day.push(i);
    }
    // 自動生成小時選項，小於10的數字前面補0
    for (let i = 0; i < 24; i++) {
      if (i < 10) this.optionsData.hour.push("0" + i);
      else this.optionsData.hour.push(i);
    }
    // 自動生成分鐘選項，5為一個區間，小於10的數字前面補0
    for (let i = 0; i < 60; i += 5) {
      if (i < 10) this.optionsData.minute.push("0" + i);
      else this.optionsData.minute.push(i);
    }
    // 預設選擇
    this.selectedData.year = now.getFullYear() - 1911;
    this.selectedData.month = now.getMonth() + 1 < 10 ? "0" + (now.getMonth() + 1) : now.getMonth() + 1;
    this.selectedData.day = now.getDate() < 10 ? "0" + now.getDate() : now.getDate();
    this.selectedData.hour = now.getHours() < 10 ? "0" + now.getHours() : now.getHours();
    // 分鐘為5的倍數
    this.selectedData.minute =
      now.getMinutes() - (now.getMinutes() % 5) < 10 ? "0" + (now.getMinutes() - (now.getMinutes() % 5)) : now.getMinutes() - (now.getMinutes() % 5);
  },
  computed: {
    // alert開關連動狀態功能
    alertVisible: {
      get() {
        return this.$store.state.alertVisible;
      },
      set(value) {
        this.$store.commit("SET_ALERT_VISIBLE", value);
      },
    },
  },
  watch: {
    alertCheck: function (val) {
      if (val.isClick) {
        if (val.OKorCancel === "OK") {
          if (this.funcNum === 0) {
            return;
          } else if (this.funcNum === 1) {
            // 錯誤提示-關閉
            this.hideAlert();
          } else if (this.funcNum === 2) {
            // 功能2-[修改完成]
            // 頁面處理
            this.hideAlert();
            this.$router.go(-1);
          }
        } else if (val.OKorCancel === "Cancel") {
          this.hideAlert();
        }
      }
    },
  },
};
</script>

<style lang="scss"></style>
