<!-- 訊息提示框 -->
<template>
  <svg xmlns="http://www.w3.org/2000/svg" style="display: none">
    <symbol id="check-circle-fill" fill="currentColor" viewBox="0 0 16 16">
      <path
        d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zm-3.97-3.03a.75.75 0 0 0-1.08.022L7.477 9.417 5.384 7.323a.75.75 0 0 0-1.06 1.06L6.97 11.03a.75.75 0 0 0 1.079-.02l3.992-4.99a.75.75 0 0 0-.01-1.05z"
      />
    </symbol>
    <symbol id="info-fill" fill="currentColor" viewBox="0 0 16 16">
      <path
        d="M8 16A8 8 0 1 0 8 0a8 8 0 0 0 0 16zm.93-9.412-1 4.705c-.07.34.029.533.304.533.194 0 .487-.07.686-.246l-.088.416c-.287.346-.92.598-1.465.598-.703 0-1.002-.422-.808-1.319l.738-3.468c.064-.293.006-.399-.287-.47l-.451-.081.082-.381 2.29-.287zM8 5.5a1 1 0 1 1 0-2 1 1 0 0 1 0 2z"
      />
    </symbol>
    <symbol id="exclamation-triangle-fill" fill="currentColor" viewBox="0 0 16 16">
      <path
        d="M8.982 1.566a1.13 1.13 0 0 0-1.96 0L.165 13.233c-.457.778.091 1.767.98 1.767h13.713c.889 0 1.438-.99.98-1.767L8.982 1.566zM8 5c.535 0 .954.462.9.995l-.35 3.507a.552.552 0 0 1-1.1 0L7.1 5.995A.905.905 0 0 1 8 5zm.002 6a1 1 0 1 1 0 2 1 1 0 0 1 0-2z"
      />
    </symbol>
  </svg>
  <div class="container">
    <div class="alert row" :class="typeColor1" role="alert" v-show="$store.state.alertVisible">
      <div class="col-12">
        <svg class="bi flex-shrink-0 me-2" width="24" height="24" role="img" aria-label="Success:" v-show="MSGTYPE === 'primary'">
          <use xlink:href="#check-circle-fill" />
        </svg>
        <svg class="bi flex-shrink-0 me-2" width="24" height="24" role="img" aria-label="Info:" v-show="MSGTYPE === 'warning'">
          <use xlink:href="#info-fill" />
        </svg>
        <svg class="bi flex-shrink-0 me-2" width="24" height="24" role="img" aria-label="Warning:" v-show="MSGTYPE === 'danger'">
          <use xlink:href="#exclamation-triangle-fill" />
        </svg>
        <h4 class="alert-heading d-inline m-1">{{ TITLE }}</h4>
      </div>
      <div class="col-12"><hr /></div>
      <div class="col-12">
        <ul>
          <li v-for="(item, i) in TEXT" :key="i">{{ item }}</li>
        </ul>
      </div>
      <div class="col-12 d-grid gap-2" v-if="MODE === 'OKOnly'">
        <button type="button" :class="typeColor2" @click="$emit('alertCheck', true, 'OK')">確定</button>
      </div>
      <div class="col-6 d-grid gap-2" v-if="MODE === 'OKCancel'">
        <button type="button" :class="typeColor2" @click="$emit('alertCheck', true, 'OK')">確定</button>
      </div>
      <div class="col-6 d-grid gap-2" v-if="MODE === 'OKCancel'">
        <button type="button" class="btn btn-secondary btn-sm" @click="$emit('alertCheck', true, 'Cancel')">取消</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: ":Alert",
  props: {
    MODE: String,
    MSGTYPE: String,
    TITLE: String,
    TEXT: Array,
  },
  emits: ["alertCheck"],
  data() {
    return {};
  },
  methods: {},
  computed: {
    typeColor1: function () {
      if (this.MSGTYPE === "primary") return "alert-primary"; // 用於成功提示
      if (this.MSGTYPE === "warning") return "alert-warning"; // 用於警告提示
      if (this.MSGTYPE === "danger") return "alert-danger"; // 用於錯誤提示
    },
    typeColor2: function () {
      if (this.MSGTYPE === "primary") return "btn btn-primary btn-sm"; // 用於成功提示
      if (this.MSGTYPE === "warning") return "btn btn-warning btn-sm"; // 用於警告提示
      if (this.MSGTYPE === "danger") return "btn btn-danger btn-sm"; // 用於錯誤提示
    },
  },
  watch: {},
  /*
使用方法

<!-- 訊息提示框 -->
<Alert
  :MODE="MSG.MODE"
  :MSGTYPE="MSG.TYPE"
  :TITLE="MSG.TITLE"
  :TEXT="MSG.TEXT"
  @alert-check="(isClick, OKorCancel) => (alertCheck = { isClick, OKorCancel })"
/>

import Alert from '@/components/Alert.vue'

components: {
    Alert,
},

**data**
// 訊息提示框資料容器
MSG: {
    MODE: '',
    TYPE: '',
    TITLE: '',
    TEXT: []
},
// 訊息確認[開關]
alertCheck: false,
// 訊息功能碼
funcNum:-1,

**methods**
  hideAlert() {
      this.$store.commit("SET_ALERT_VISIBLE", false);
      this.alertVisible = false;
    },

**computed**
// alert開關連動狀態功能
alertVisible: {
    get () {
      return this.$store.state.alertVisible
    },
    set (value) {
      this.$store.commit('SET_ALERT_VISIBLE', value)
    }
},

**watch**
   alertCheck: function (val) {
      if (val.isClick) {
        if (val.OKorCancel === "OK") {
          if (this.funcNum === 0) {
            return
          } else if (this.funcNum === 1) {
            // 功能1-[]
            // 頁面處理
            
          } else if (this.funcNum === 2) {
            // 功能2-[修改用戶參數-後續處理]
            this.hideAlert()
            this.$router.go(-1);
          }
        } else if (val.OKorCancel === "Cancel") {
          this.hideAlert();
        }
      }
    },

**調用
// 訊息提示框
this.alertVisible = true;
this.MSG.MODE = "OKOnly"; // 形式為：OKOnly、OKCancel
this.funcNum = 0;
this.MSG.TYPE = "primary";  // primary成功, warning警告, danger錯誤
this.MSG.TITLE = "系統提示"; // 系統提示，系統警告，系統錯誤
this.MSG.TEXT = ["修改瀏覽人次成功。"]; // 內容

*/
};
</script>

<style lang="scss">
.alert {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 9998;
}
</style>
